
/*
 * Play Page
 */
.playpage-container {
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;

  position: absolute;
  top: 0px;

  .canvas {
    flex: 1;
    min-height: 100%;
    min-width: 90%;

    flex-direction: column;
    display: flex;
    justify-content: space-around;

    .infoArea {
      min-width: 90%;
      min-height: 150px;
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;

      .question {
        width: 100%;
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;

        transition: all 500ms ease-in-out;
        opacity: 1;

        h1 {
          font-weight: 100;
        }
      }

      .iching-card {
        flex: 1 1 100%;
        align-self: center;
        width: 100%;

        .hexagram-card {
          cursor: pointer;
          border-radius: 2px;
        }
      }


    }

    .ichingDragArea {
      min-width: 100%;
      height: 80%;
      text-align: center;

      flex: 3;
      display: flex;
      justify-content: center;
      flex-direction: row;

      .gongo {
        border-radius: 50%;
        width: 240px;
        height: 240px;

        box-shadow: 0px 0px 20px #000;
        border: none;
        cursor: pointer;
        background-color: transparent;
        background-image: url(/assets/img/iching-daylight.png);
        background-size: contain;

        will-change: transform;
        transform: scale(1.0);

        animation: forwards;

        &.down {
          transform: scale(0.95);
          box-shadow: 0px 0px 10px #000;
          transition: all 350ms ease-in-out;
          transform: rotate(-30deg);
        }

        &.hit {
          -moz-animation: spin 4.0s ease-out;
          -webkit-animation: spin 4.0s ease-out;
        }
      }

    }
  }

}



